h1, p {
  font-family: Lato;
}

.table {
  width: auto;
}

.score-cell {
  white-space: nowrap;
}

.result-cell {
  background: #ddd;
}

.winner-ok {
  background: #5cc55c;
}

.score-ok {
  background: #f9f422;
}

.no-points {
  background: #fb8d8d;
}

.winner {
  margin-bottom: 20px;
}


